'use client';
import React from 'react';
import { Controller, useFormContext } from 'react-hook-form';
import TextMaskCustom from './TextMaskInput';
import { StyledInputWrapper, StyledLabel, StyledTextField } from './styled';

interface PhoneInputConfig {
  name: string;
  placeholder: string;
  label?: string;
}

const config: Record<string, PhoneInputConfig> = {
  phone: {
    name: 'phone',
    placeholder: '1234567890',
    label: 'Phone number',
  },
};

interface PhoneInputProps {
  id: string;
}

export const PhoneInputComponent = ({ id }: PhoneInputProps) => {
  const { name, placeholder, label } = config[id];
  const { control } = useFormContext();

  return (
    <Controller
      name={name}
      control={control}
      render={({ field, fieldState: { error } }) => (
        <StyledInputWrapper>
          <StyledLabel htmlFor={name}>{label}</StyledLabel>
          <StyledTextField
            {...field}
            id={name}
            variant="outlined"
            error={!!error}
            helperText={error ? error.message : ''}
            fullWidth
            placeholder={placeholder}
            InputProps={{
              inputComponent: TextMaskCustom as unknown as React.ElementType,
            }}
          />
        </StyledInputWrapper>
      )}
    />
  );
};
